
 import XHeader from 'vux/src/components/x-header/index.vue'
import XInput from 'vux/src/components/x-input/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'
import Group from 'vux/src/components/group/index.vue'
import Box from 'vux/src/components/box/index.vue'
import Toast from 'vux/src/components/toast/index.vue'

export default{
  data(){
    return{
      name:"",//文本框输入数据
      toastShow:false,
      toastText:"",
      toastType:"warn"
    }
  },
  mounted: function () {
    var _this=this;
    /* 第一个输入框取得焦点 */
    var inputs=document.getElementById('change-name').getElementsByTagName('input');
    inputs[0].focus();
    plus.key.addEventListener("backbutton",function(){
      _this.back();
    });
  },
  methods:{
    back(){
      this.$router.push("/personal");
    },
    confirm(){
      var _this=this;
      if(this.name==""){
        this.toastShow=true;
        this.toastText="昵称不能为空";
        this.toastType="warn";
      }else if(this.name.length<10){
        if(this.name=="全部"||this.name.indexOf("(")!=-1||this.name.indexOf("）")!=-1){
          this.toastShow=true;
          this.toastText="昵称不能使用关键字和特殊字符！";
          this.toastType="warn";
        }else{
          //修改昵称
          this.$instance.post(
            'user/update',{
              username:_this.name
            })
            .then(function(response){
              if(response.status==200){
                var res=response.data;
                if(res.code==200){
                  _this.toastShow=true;
                  _this.toastText="昵称修改成功";
                  _this.toastType="default";
                }else{
                  _this.toastShow=true;
                  _this.toastText=res.message;
                  _this.toastType="warn";
                }
              }
            })
            .catch(function(err){
              console.log(err);
            });
        }

      }else{
        this.toastShow=true;
        this.toastText="字符长度过长";
        this.toastType="warn";
      }
    }
  },
  components:{
    XHeader,
    XInput,
    XButton,
    Group,
    Box,
    Toast
  }
}



