
  import hex_md5 from '../assets/js/md5.js'
  import XHeader from 'vux/src/components/x-header/index.vue'
import Box from 'vux/src/components/box/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'
import XInput from 'vux/src/components/x-input/index.vue'
import Group from 'vux/src/components/group/index.vue'
import Popover from 'vux/src/components/popover/index.vue'
import Toast from 'vux/src/components/toast/index.vue'

export default{
  data(){
    return{
      newPSD:"",
      againPSD:"",
      //提示框属性数据
      toastShow:false,
      toastText:"",
      textShow:false,
      text:"",
      cellPhone:""
    }
  },
  methods:{
    back(){
      this.$router.push("/lookPSD");
    },
    //测试完成
    confirm(){
      var _this=this;
      //验证均不为空，验证密码格式，验证是否相等
      if(this.newPSD==""){
        this.toastShow=true;
        this.toastText="新密码不能为空"
      }else if(this.againPSD==""){
        this.toastShow=true;
        this.toastText="确认密码不能为空"
      }else{
        //验证密码格式是否正确(长度在6-16位,由字母、数字、特殊符号组成)
        var psd = /^(?=.*[a-zA-Z])(?=.*\d)(?=.*[~!@#$%^&*()_+`\-={}:";'<>?,.\/]).{6,16}$/;
        var re = new RegExp(psd);
        if(re.test(this.newPSD)){
          //验证两者是否相等
          if(this.newPSD==this.againPSD){
            //密码加密
            var md5Psd=hex_md5(this.newPSD);
            //发送请求
            console.log(_this.cellPhone);
            this.$instance.post(
              'user/setNewPSD',{
                phone:_this.cellPhone,
                password:md5Psd
              })
              .then(function(response){
                console.log(response);
                console.log(response.data);
                if(response.status==200){
                  var res=response.data;
                  if(res.code==200){
                    _this.textShow=true;
                    _this.text="密码修改成功，转到登陆页面"
                    //转到登陆页面
                    setTimeout(function(){
                      _this.$router.push("/login");
                    },3000);
                  } else{
                    _this.toastShow=true;
                    _this.toastText=res.message+"密码修改失败！"
                  }
                }
              })
              .catch(function(err){
                console.log(err);
              });
          }else{
            this.toastShow=true;
            this.toastText="两者不一致"
          }
        }else{
          this.toastShow=true;
          this.toastText="新密码格式错误"
        }
      }
    }
  },
  components:{
    XHeader,
    Box,
    XButton,
    XInput,
    Group,
    Popover,
    Toast
  },
  mounted: function () {
    this.cellPhone=this.$store.state.cellPhone;
    /* 第一个输入框取得焦点 */
    var set_PSD=document.getElementById('set_PSD').getElementsByTagName('input');
     set_PSD[0].focus();
    var _this=this;
    plus.key.addEventListener("backbutton",function(){
      _this.back();
    });
  }
}




