
import Swiper from 'vux/src/components/swiper/swiper.vue'
import XButton from 'vux/src/components/x-button/index.vue'
import SwiperItem from 'vux/src/components/swiper/swiper-item.vue'


const imgList = [
  'http://placeholder.qiniudn.com/754x754/FF3B3B/ffffff',
  'http://placeholder.qiniudn.com/754x754/FFEF7D/ffffff',
  'http://placeholder.qiniudn.com/754x754/8AEEB1/ffffff'
]
const demoList = imgList.map((one, index) => ({
  url: 'javascript:',
  img: one
}))
export default {
  components: {
    Swiper,
    XButton,
    SwiperItem
  },
  data () {
    return {
      // note: changing this line won't causes changes
      // with hot-reload because the reloaded component
      // preserves its current state and we are modifying
      // its initial state.
      demo03_list:imgList,
      swiperItemIndex: 0
    }
  },
  methods:{
    jump(){
      this.$router.push('/home');
    }
  },
  created:function () {
    var self=this;
    setTimeout(function(){
      self.$router.push('/home');
    },3000);
  }

}



