
import XHeader from 'vux/src/components/x-header/index.vue'
import Group from 'vux/src/components/group/index.vue'
import Cell from 'vux/src/components/cell/index.vue'
import Icon from 'vux/src/components/icon/index.vue'
import XInput from 'vux/src/components/x-input/index.vue'
import InlineCalendar from 'vux/src/components/inline-calendar/index.vue'
import Picker from 'vux/src/components/picker/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'
import XProgress from 'vux/src/components/x-progress/index.vue'
import Confirm from 'vux/src/components/confirm/index.vue'
import Toast from 'vux/src/components/toast/index.vue'
import Calendar from 'vux/src/components/calendar/index.vue'

const payItems=['餐饮', '交通', '购物', '娱乐', '医教', '居家', '投资', '人情', '生意'];
export default{
  data(){
    return{
      currentDate:"",//当前选中日期，默认为空，即选中当天日期
      budgets:"0.0",//总预算
      use:"0.0",//已用
     unUse:"0.0",//未用
      detailMoney:"0.0",//输入金额
      isSaveShow:false,//修改确认框是否显示
      isShow:false,//预算项选择框是否显示
      payItem:["餐饮"],//当前选择的某一预算项
      payItems:[['餐饮', '交通', '购物', '娱乐', '医教', '居家', '投资', '人情', '生意']],//修改预时可选择的全部预算项
      payItemText:"餐饮金额：",
      //每一项预算百分比、余额、预算，数据结构
      budgetDetail:[//不存在，显示全为0.0
        {name:"餐饮",budget: "0.0",percent:0,balance: "0",img:"http://115.159.188.223/budget/food.png"},{name:"交通",budget: "0.0",percent:0,balance: "0.0",img:"http://115.159.188.223/budget/traffic.png"},{name:"购物",budget: "0.0",percent:0,balance: "0.0",img:"http://115.159.188.223/budget/shop.png"},
        {name:"娱乐",budget: "0.0",percent:0,balance: "0",img:"http://115.159.188.223/budget/play.png"}, {name:"医教",budget: "0.0",percent:0,balance: "0.0",img:"http://115.159.188.223/budget/hospital.png"},{name:"居家",budget: "0.0",percent:0,balance: "0.0",img:"http://115.159.188.223/budget/home.png"},
        {name:"投资",budget: "0.0",percent:0,balance: "0",img:"http://115.159.188.223/budget/mana-mon.png"},{name:"人情",budget: "0.0",percent:0,balance: "0.0",img:"http://115.159.188.223/budget/favor.png"},{name:"生意",budget: "0.0",percent:0,balance: "0.0",img:"http://115.159.188.223/budget/business.jpg"},
      ],

      //提示信息框设置
      toastText:"",
      toastShow:false,
      toastType:"warn",

      //修改信息确认
      changeItem:"餐饮",

    }
  },
  methods:{
    back(){
      this.$router.push("/home");
    },
    selectPay(){
      if(this.isShow){
        this.isShow=false;
      }else{
        this.isShow=true;
      }
      console.log("sss");
    },
    payItemChange(){
      //this.isShow=false;
     this.changeItem=this.payItem[0];
      console.log("ddd");
      this.payItemText=this.payItem[0]+"金额："
    },
    monthChange(){
      var month=new Date(this.currentDate);
      var currentMonth=new Date(this.$store.state.todayDate);
     if(month<currentMonth){
       document.getElementById("setBudget").style.display="none";
       document.getElementById("details").className="budget-detail-hide";
     }else{
       document.getElementById("setBudget").style.display="block";
       document.getElementById("details").className="budget-detail";
     }

      this.reload();
    },
    save(){
      //金额设置
      var num =/^(([1-9]{1}\d*)|([0]{1}))(\.(\d){1,1})?$/;
      var re = new RegExp(num);
      var money=this.detailMoney;
      if(money==""){
        this.toastShow=true;
        this.toastType="warn";
        this.toastText="金额不能为空";
      }else if(money<0){
        this.toastShow=true;
        this.toastType="warn";
        this.toastText="金额不能小于0";
      }else if(re.test(money)){
        this.isSaveShow=true;
      }else {
        this.toastShow=true;
        this.toastType="warn";
        this.toastText="金额格式错误";
      }


    },
    onCancel(){
      this.isSaveShow=false;
    },
    onConfirm(){
      var month=new Date(this.currentDate).getMonth()+1;
      var year = new Date(this.currentDate).getYear(); //当前年
      year += (year < 2000) ? 1900 : 0;
      var _this=this;
      //执行请求，修改预算，
     this.$instance.post('budget/addBudget',{
        num:_this.detailMoney,
        month:month,
       year:year,
        typeName:_this.payItem[0],
      }).then(function(response){
        if(response.status==200){
          var res=response.data;
          if(res.code===200){
            _this.toastShow=true;
            _this.toastType="default";
            _this.toastText="设置成功";
            _this.reload();//刷新页面数据
          }else if(res.code===404){}
          _this.toastShow=true;
          _this.toastType="warn";
          _this.toastText=res.message;
        }
      })
        .catch(function(err){
          console.log(err);
        });
      this.isSaveShow=false;//修改确认框关掉
    },
    //重新加载方法，同步更新显示数据
    reload(){
      var _this=this;
      var month=new Date(this.currentDate).getMonth()+1;
      var year = new Date(this.currentDate).getYear(); //当前年
      year += (year < 2000) ? 1900 : 0;
      this.$instance.post('budget/getBudgets',{  //获取请求数据，更新页面
        month:month, year:year
      }).then(function(response){
        if(response.status==200){
          var res=response.data;
          if(res.code===200){
            _this.budgets=res.budgets;
            _this.use=res.use;
            _this.unUse=res.unUse;
            if(res.budgetDetails.length>0){
              _this.budgetDetail=res.budgetDetails;//存在详细预算，直接设置
            }else{
              _this.budgetDetail=[//不存在，显示全为0.0
                {name:"餐饮",budget: "0.0",percent:0,balance: "0",img:"http://115.159.188.223/budget/food.png"},{name:"交通",budget: "0.0",percent:0,balance: "0.0",img:"http://115.159.188.223/budget/traffic.png"},{name:"购物",budget: "0.0",percent:0,balance: "0.0",img:"http://115.159.188.223/budget/shop.png"},
                {name:"娱乐",budget: "0.0",percent:0,balance: "0",img:"http://115.159.188.223/budget/play.png"}, {name:"医教",budget: "0.0",percent:0,balance: "0.0",img:"http://115.159.188.223/budget/hospital.png"},{name:"居家",budget: "0.0",percent:0,balance: "0.0",img:"http://115.159.188.223/budget/home.png"},
                {name:"投资",budget: "0.0",percent:0,balance: "0",img:"http://115.159.188.223/budget/mana-mon.png"},{name:"人情",budget: "0.0",percent:0,balance: "0.0",img:"http://115.159.188.223/budget/favor.png"},{name:"生意",budget: "0.0",percent:0,balance: "0.0",img:"http://115.159.188.223/budget/business.jpg"},
              ];
            }
          }else if(res.code===404){
          }
        }
      })
        .catch(function(err){
          console.log(err);
        });
      this.detailMoney="0.0";

    }
  },
  mounted:function () {
    var _this=this;
    this.currentDate=this.$store.state.todayDate;
    this.reload();
    plus.key.removeEventListener("backbutton",function(){
      console.log("remove");
    });
    plus.key.addEventListener("backbutton",function(){
      _this.back();
    });
  },
  components:{
    XHeader,
    Group,
    Cell,
    Icon,
    XInput,
    InlineCalendar,
    Picker,
    XButton,
    XProgress,
    Confirm,
    Toast,
    Calendar
  }
}



