
import clip from '../assets/js/clip';
import XHeader from 'vux/src/components/x-header/index.vue'
import Group from 'vux/src/components/group/index.vue'
import Cell from 'vux/src/components/cell/index.vue'
import Box from 'vux/src/components/box/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'
import Actionsheet from 'vux/src/components/actionsheet/index.vue'
import Toast from 'vux/src/components/toast/index.vue'
import Loading from 'vux/src/components/loading/index.vue'

export default{
  data(){
    return {
      name: "85805261mo",//昵称
      cellphone: "18580516162",//手机号
      show: false,//头像选择actionSheet是否展示
      menus: {     //头像选择actionSheet菜单
        select: '选择头像',
      },
      //裁剪属性
      isClip: false,
      clipUrl: '',
      noScoll: function (evt) {
        this.isClip && evt.preventDefault();
      },
      clip: {},

      //显示警告信息
      toastShow: false,
      toastText: "",
      toastType: "warn",

      showLoading:false
    }
  },
  methods: {
    back(){
      this.$router.push("/home");
    },
    exit(){
      var _this = this;
      //退出登陆
      this.$instance.get('user/loginOut').then(function (response) {
        console.log(response);
        console.log(response.data);
        if (response.status == 200) {
          var res = response.data;
          if (res.code === 200) {
            //转到主页
            _this.$router.push("/home");
          } else if (res.code === 400) {

          }
        }
      })
        .catch(function (err) {
          console.log(err);
        });

    },
    selectImg(){
      this.show = true;
    },
    selectHeadImg(){
      this.galleryImg();
    },
    //相册选取图片
    galleryImg() {
      var _this = this;
      plus.gallery.pick(function (path) {
        //path：返回选择的图片路径
        // 通过URL参数获取目录对象或文件对象
        //entry:请求到的目录或文件对象
        plus.io.resolveLocalFileSystemURL(path, function (entry) {
          //压缩图片
          _this.compressImage(entry.toLocalURL(), entry.name);
        }, function (e) {
          this.toastShow = true;
          this.toastText = "读取相册文件错误：" + e.message;
          this.toastType = "warn";
        });
      }, function (e) {
      }, {
        filename: "_doc/camera/",
        filter: "image"
      });
    },
    //压缩图片
    compressImage(url, filename){
      var _this = this;
      var name = "_doc/app/" + filename;//_doc/upload/F_ZDDZZ-1467602809090.jpg
      plus.zip.compressImage({
          src: url,//src: (String 类型 )压缩转换原始图片的路径
          dst: name,//压缩转换目标图片的路径
          quality: 20,//quality: (Number 类型 )压缩图片的质量.取值范围为1-100
          overwrite: true//overwrite: (Boolean 类型 )覆盖生成新文件
        },
        //可通过event.target（String类型）获取压缩转换后的图片url本地绝对路径
        function (event) {
          var path = name;//压缩转换目标图片的本地相对路径
          _this.clipImg(event.target);//裁剪图片
        }, function (error) {
          this.toastShow = true;
          this.toastText = "图片压缩失败，请稍后再试";
          this.toastType = "warn";
        });
    },
    //裁剪图片
    clipImg(url){
      var _this=this;
      this.clip = new clip('container', _this);
      this.clip.init(url);
      this.isClip = true;
      document.body.addEventListener('touchmove', this.noScoll, false);
    },
    saveImg(){
      var _this=this;
      _this.isClip = false;
      this.showLoading=true;
      this.clip.save();
      setTimeout(function(){
        _this.uploadImg(_this.clipUrl);
        //document.getElementsByTagName("img")[0].src=_this.clipUrl;
        //console.log(_this.clipUrl);
      },3000);
      document.body.removeEventListener('touchmove', this.noScoll, false);

    },
    uploadImg(url){
      var _this=this;
     /* var canvas = document.createElement('CANVAS'),
        ctx = canvas.getContext('2d'),
        img = new Image;
      img.crossOrigin = 'Anonymous';
      img.onload = function(){
        canvas.height = img.height;
        canvas.width = img.width;
        ctx.drawImage(img,0,0);
        var dataURL = canvas.toDataURL();
        canvas = null;
        //上传服务器
        _this.$instance.post('pic/upload',{
          uploadFile:dataURL
        }).then(function(response){
            if(response.status==200){
              var res=response.data;
              console.log(response);
              if(res.code==200){
                document.getElementsByTagName("img")[0].src=res.url;
              }else{
              }
            }else{
            }
          })
          .catch(function(err){
            console.log(err);
          });

      };
      img.src = require("../assets/logo.png");*/
      //上传服务器
      _this.$instance.post('pic/upload',{
        uploadFile:url
      }).then(function(response){
        if(response.status==200){
          var res=response.data;
          if(res.code==200){
            document.getElementsByTagName("img")[0].src=res.url;
            setTimeout(function(){
              _this.showLoading=false;
            },3000);
          }else{
          }
        }else{
        }
      })
        .catch(function(err){
          console.log(err);
        });
    },
    reload(){
      //获取昵称、手机号 测试完成
      //获取头像、昵称、手机号
      var _this = this;
      this.$instance.get('user/getUser').then(function (response) {
        if (response.status == 200) {
          var res = response.data;
          console.log(res);
          if (res.code === 200) {
            _this.name = res.user.username;
            _this.cellphone = res.user.phone;
            document.getElementsByTagName("img")[0].src=res.user.icon;
          } else if (res.code === 400) {
            console.log(res.message);
          }
        }
      })
        .catch(function (err) {
          console.log(err);
        });
    },
    //滑动事件添加
    load(){
      var _this=this;
      var startX,startY,moveEndX,moveEndY,X,Y;
      document.addEventListener('touchstart',touch, false);
      document.addEventListener('touchmove',touch, false);
      document.addEventListener('touchend',touch, false);
      function touch (event){
        var event = event || window.event;
        switch(event.type){
          case "touchstart":
            event.preventDefault();
            startX=event.touches[0].clientX;
            startY=event.touches[0].clientY;
            break;
          case "touchmove":
            break;
          case "touchend":
            event.preventDefault();
            moveEndX=event.changedTouches[0].clientX;
            moveEndY=event.changedTouches[0].clientY
            X=moveEndX-startX;
            Y=moveEndY-startY;
            if ( Math.abs(X) > Math.abs(Y) && X > 0 ) {

            }
            else if ( Math.abs(X) > Math.abs(Y) && X < 0 ) {
              _this.$router.push("/detail");
            }
            else if ( Math.abs(Y) > Math.abs(X) && Y > 0) {
              //console.log("top 2 bottom");
            }
            else if ( Math.abs(Y) > Math.abs(X) && Y < 0 ) {
              //console.log("bottom 2 top");
            }
            else{
              //console.log("just touch");
            }
            break;
        }
      }
    }
  },
  mounted: function () {
    //获取昵称、手机号 测试完成
    //获取头像、昵称、手机号
    this.reload();
    var _this=this;
    plus.key.removeEventListener("backbutton",function(){
      console.log("remove");
    });
    plus.key.addEventListener("backbutton",function(){
      _this.back();
    });
    //页面滑动
    //window.addEventListener('load',this.load(), false);
  },
  components: {
    XHeader,
    Group,
    Cell,
    Box,
    XButton,
    Actionsheet,
    Toast,
    Loading
  }
}



